<template>
  <v-app>
  
    <v-toolbar v-if="Load_currentUser" :clipped-left="$vuetify.breakpoint.lgAndUp" color="primary" dark app fixed style="box-shadow: none">
      <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
         <v-btn icon large>
        <v-avatar size="32px" tile>
          <img src="./logo.png" alt="HiMenu">
        </v-avatar>
      </v-btn>
        <span class="hidden-sm-and-down">HiMenu</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <!-- <v-menu v-model="menu" :close-on-content-click="false" :nudge-width="200" offset-y>
        <v-list class="pa-1" style="background: transparent" slot="activator">
          <v-list-tile avatar>
            <v-list-tile-avatar>
              <img :src="Load_currentUser.photoURL">
            </v-list-tile-avatar>
          </v-list-tile>
        </v-list>

        <v-card>
          <v-list class="pa-1">
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <img :src="Load_currentUser.photoURL">
              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title>{{ Load_currentUser.displayName}}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-card>
      </v-menu> -->
         <v-menu bottom left>
            <template v-slot:activator="{ on }">
              <v-btn
                dark
                icon
                v-on="on"
              >
                <v-icon>more_vert</v-icon>
              </v-btn>
            </template>

            <v-list subheader>
         
           <v-list-tile avatar>
          <v-list-tile-avatar>
            <img :src="Load_currentUser.photoURL">
          </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>{{ Load_currentUser.displayName}}</v-list-tile-title>
            </v-list-tile-content>
        </v-list-tile>
        <v-list-tile
            avatar
            @click=""
          >
            <v-list-tile-action>
              <v-icon color="light-blue">fa fa-sign-out-alt</v-icon>
            </v-list-tile-action>

            <v-list-tile-content>
              <v-list-tile-title>Logout</v-list-tile-title>
            </v-list-tile-content>

            <v-list-tile-action>
              <!-- <v-icon :color="item.active ? 'teal' : 'grey'">chat_bubble</v-icon> -->
              <v-icon color="light-grey">fa fa-chevron-right</v-icon>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>
          </v-menu>
      <!-- <v-btn icon>
        <v-icon>notifications</v-icon>
      </v-btn>-->
      <!-- @click="SignOut" -->
      <!-- <v-btn icon>
        <v-icon>exit_to_app</v-icon>
      </v-btn> -->

     
    </v-toolbar>
    <v-content style="padding: 0px; background: #fff">
      <v-container grid-list-md style="padding-top: 10vh; background: #fff">
        <v-layout row wrap>
          <v-flex xs12 sm12>
            <router-view></router-view>
           </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import firebase from "firebase";

// const axios = require("axios");
import { mapGetters } from "vuex";
import { mapState } from "vuex";
import { mapWaitingActions, mapWaitingGetters } from "vue-wait";

export default {
 
  mounted() {
   
  },
 computed: {
   ...mapGetters([
     "Load_currentUser"
      // ...
         ]),

  },
  data: () => ({
   dialog: false,
        fav: true,
        menu: false,
  }),
  props: {
  },
  methods: {
     SignOut() {
      let vm = this;
      firebase
        .auth()
        .signOut()
        .then(
          function() {
            // Sign-out successful.
            vm.$router.replace("/login");
          },
          function(error) {
            // An error happened.
            alert("Oops, error! try again");
          }
        );
    },
  }
};
</script>


