<template>
  <v-container>
    <!-- <v-parallax
      height="140"
      src="https://cdn.vuetifyjs.com/images/parallax/material2.jpg"
 
    >
    </v-parallax>-->
    <v-toolbar absolute scroll-off-screen scroll-target="#scrolling-techniques">
      <v-btn icon flat @click="$router.go(-1)">
        <v-icon>arrow_back</v-icon>
      </v-btn>

      <!-- <v-btn icon flat @click="$router.go(-1)">
          <v-icon>arrow_back</v-icon>
      </v-btn>-->
      <v-toolbar-title>HiOffers</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
    <v-layout row style>
      <v-flex xs12 sm12>
        <v-card :raised="false" style="box-shadow: none; border-radius: 0px;padding-top: 5vh">
          <v-card flat>
            <v-btn @click="$router.push('newoffer')" color="primary" dark class="white--text">
              Add Offer
              <v-icon right dark>cloud_upload</v-icon>
            </v-btn>
            <v-tabs
              centered
              color="orange"
              icons-and-text
              next-icon="mdi-arrow-right-bold-box-outline"
              prev-icon="mdi-arrow-left-bold-box-outline"
              show-arrows
            >
              <v-tabs-slider></v-tabs-slider>

              <v-tab
                v-for="offer in offers"
                :key="offer.name"
                :href="'#day-'+offer.name"
              >{{offer.name}}</v-tab>
              <v-tabs-items>
                <v-tab-item v-for="offer in offers" :key="offer.name" :value="'day-'+offer.name">
                  <v-card flat>
                    <v-card-text>
                      <v-layout row wrap>
                        <v-flex
                          xs12
                          sm12
                          md4
                          v-for="myoffer in offer.data"
                          :key="myoffer.title"
                          style="padding:10px;"
                        >
                          <!-- {{ myoffer }} -->
                          <v-hover>
                            <v-card
                              slot-scope="{ hover }"
                              class="mx-auto"
                              color="grey lighten-4"
                              max-width="600"
                            >
                              <v-img :aspect-ratio="16/9" :src="myoffer.image_url">
                                <v-expand-transition>
                                  <div
                                    v-if="hover"
                                    class="d-flex transition-fast-in-fast-out orange darken-2 v-card--reveal display-3 white--text"
                                    style="height: 100%;"
                                  >Kes {{ myoffer.price }}</div>
                                </v-expand-transition>
                              </v-img>
                              <v-card-text class="pt-4" style="position: relative;">
                                <v-btn absolute color="orange" class="white--text" fab right top>
                                  <v-icon>edit</v-icon>
                                </v-btn>

                                <!-- <div class="font-weight-light grey--text  mb-2"></div> -->
                                <h3
                                  class="font-weight-light orange--text title mb-2"
                                >{{ myoffer.title}}</h3>
                                <div class="font-weight-light mb-2">{{ myoffer.description}}</div>
                              </v-card-text>
                            </v-card>
                          </v-hover>
                        </v-flex>
                      </v-layout>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
              </v-tabs-items>
            </v-tabs>
          </v-card>

         
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>

import firebase from "../service/firebase";
import { VclTwitch } from "vue-content-loading";



export default {
  components: {
    VclTwitch
  },
  data() {
    return {
      menu: null,
      offers: null,
    };
  },
  computed: {
 
  },
  mounted() {
    // this.menu = find(this.$root.global_menus, (menu) => menu['.key'] === this.$route.params.menu_id)
    let menu = firebase.database.ref(
      "global_menus/" + this.$route.params.menu_id
    );

    let offers = firebase.database
      .ref("global_menus/" + this.$route.params.menu_id)
      .child("offers");
    // console.log(obj);
    let vm = this;
    menu.on("value", function(snapshot, prevChildKey) {
      var newMenu = snapshot.val();
      vm.menu = newMenu;
      // console.log(newPost);
    });

    offers.on("value", function(snapshot, prevChildKey) {
      // vm.menu = newPost
      var newOffer = snapshot.val();
      vm.offers = newOffer;
      console.log(newOffer);
    });
    //  categories.on("child_added", function(snapshot, prevChildKey) {
    //   // vm.menu = newPost
    //    var newCategory = snapshot.val();
    //   vm.categories = newCategory
    //   console.log(newCategory);

    // });
  },
  methods: {
   
  }
};
</script>

<style>
.v-image__image--cover {
  background-size: cover;
}
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: 0.5;
  position: absolute;
  width: 100%;
}
</style>



