<template>

<v-container>
   <!-- <v-parallax
      height="140"
      src="https://cdn.vuetifyjs.com/images/parallax/material2.jpg"
 
    >
   
    </v-parallax> -->
      <v-toolbar style="box-shadow: none;" color="primary" dark absolute scroll-off-screen scroll-target="#scrolling-techniques">
      <v-btn icon flat @click="$router.go(-1)">
        <v-icon>arrow_back</v-icon>
      </v-btn>

      <!-- <v-btn icon flat @click="$router.go(-1)">
          <v-icon>arrow_back</v-icon>
      </v-btn>-->
      <v-toolbar-title>Category List</v-toolbar-title>
      <v-spacer></v-spacer>
     
    </v-toolbar>
  <v-layout row style="">
    <v-flex xs12 sm12>
     <v-card :raised="false" style="box-shadow: none; border-radius: 0px;padding-top: 5vh">
       <v-form>
    <v-container>
      <v-text-field v-model="search">
        <template slot="label">
          Search a <strong>Category</strong> here, use <strong>Name</strong> of category? <v-icon style="vertical-align: middle">find_in_page</v-icon>
        </template>
      </v-text-field>
      <!-- <input type="hidden" id="testing-code" v-show="false" :value="testingCode"> -->
    </v-container>
  </v-form>
  <v-card flat>
                <v-btn @click="dialog = true" color="primary" dark class="white--text">
                  Add Category
                  <v-icon right dark>cloud_upload</v-icon>
                </v-btn>

                <v-list two-line subheader v-if="categories">
                  <v-flex xs12 style="padding:0 10px;" v-if="categories.length < 1">
                    <vcl-twitch secondary="#FFC107" primary="#fb8c00"></vcl-twitch>
                  </v-flex>
                  <div v-for="category in filteredCategories" :key="category.name">
                    <v-list-tile
                      @click="$router.push('category/'+category.name)"
                      v-if="category.hasOwnProperty('name')"
                      avatar
                      v-ripple
                    >
                      <v-list-tile-avatar>
                        <v-icon class="amber white--text">call_to_action</v-icon>
                      </v-list-tile-avatar>

                      <v-list-tile-content>
                        <v-list-tile-title>{{ category.name }}</v-list-tile-title>
                      </v-list-tile-content>

                      <v-list-tile-action>
                        <v-btn icon ripple>
                          <v-icon color="grey lighten-1">chevron_right</v-icon>
                        </v-btn>
                      </v-list-tile-action>
                    </v-list-tile>
                  </div>
                </v-list>
              </v-card>
      
       
          <v-dialog v-model="dialog" width="500">
            <v-card>
              <v-card-title class="headline orange lighten-2" primary-title>New Category</v-card-title>

              <v-card-text>
                <v-form ref="form" v-model="valid" lazy-validation>
                  <v-layout row wrap style="padding: 10px">
                    <v-flex xs12 sm12 md12>
                      <v-text-field
                        v-model="categoryName"
                        :rules="nameRules"
                        label="Category Name"
                        placeholder="e.g Breakfast or Lunch"
                        required
                        box
                      ></v-text-field>
                    </v-flex>
                  </v-layout>
                </v-form>
              </v-card-text>

              <v-divider></v-divider>

              <v-card-actions>
                <v-spacer></v-spacer>
                <!-- <v-btn absolute dark fab top right color="pink">
                      <v-icon>add</v-icon>
                </v-btn>-->
                <v-btn color="red" class="white--text" @click="dialog = false">Cancel</v-btn>
                <v-btn color="primary" flat @click="addCategories">Create</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-card>
     
    </v-flex>
  </v-layout>
</v-container>

</template>

<script>

import { find } from "lodash";
import firebase from "../service/firebase";
import { VclTwitch } from "vue-content-loading";
import _ from "lodash";

export default {
  components: {
    VclTwitch,
  },
  data() {
    return {
      menu: null,
      search: '',
      categories: null,
      valid: true,
      categoryName: "",
      dialog: false,
      nameRules: [
        v => !!v || "Name is required"
        // v => (v && v.length <= 10) || 'Name must be less than 10 characters'
      ],
    };
  },
  computed: {
    
     filteredCategories() {
       let vm = this
      

      return _.filter(this.categories, function(category) {
        return category.name.toLowerCase().includes(vm.search.toLowerCase())
      });

    }
  },
  mounted() {
    // this.menu = find(this.$root.global_menus, (menu) => menu['.key'] === this.$route.params.menu_id)
    let menu = firebase.database.ref(
      "global_menus/" + this.$route.params.menu_id
    );
    let categories = firebase.database
      .ref("global_menus/" + this.$route.params.menu_id)
      .child("categories");

    let vm = this;
    menu.on("value", function(snapshot, prevChildKey) {
      var newMenu = snapshot.val();
      vm.menu = newMenu;
      // console.log(newPost);
    });
    categories.on("value", function(snapshot, prevChildKey) {
      // vm.menu = newPost
      var newCategory = snapshot.val();
      vm.categories = newCategory;
      console.log(newCategory);
    });
  },
  methods: {
  
    addCategories() {
      if (this.$refs.form.validate()) {
        let obj = firebase.database.ref(
          "global_menus/" + this.$route.params.menu_id
        );
        obj
          .child("categories")
          .child(this.categoryName)
          .set({
            name: this.categoryName
          });
        this.dialog = false;
        this.categoryName = "";
      }
    }
  }
};
</script>

<style>
.v-image__image--cover {
  background-size: cover;
}
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: 0.5;
  position: absolute;
  width: 100%;
}
</style>



