<template>
 <v-app class="hide-overflow white-bg" style="position: relative;">
           
 <v-toolbar
      absolute
      scroll-off-screen
      scroll-target="#scrolling-techniques"
      dark
      color="primary"
    >
      <v-btn icon flat @click="$router.go(-1)">
          <v-icon>arrow_back</v-icon>
      </v-btn>
      <!-- <v-btn icon flat @click="$router.go(-1)">
          <v-icon>arrow_back</v-icon>
        </v-btn> -->
      <v-toolbar-title>Customer List</v-toolbar-title>
   
      <v-spacer></v-spacer>
     
    </v-toolbar>    
     <v-card id="scrolling-techniques" class="scroll-y" style="height: 100vh;box-shadow: none; padding-top: 10vh">
      <v-container style="padding: 0px;" fluid grid-list-lg>
          <v-layout row>
    <v-flex xs12 sm12>
      <v-card :raised="false" style="box-shadow: none; box-shadow: none;  border-radius: 20px; padding: 20px">

         
<v-text-field
            flat
            solo-inverted
            hide-details
            prepend-inner-icon="search"
            label="Search Customer here"
            class="hidden-sm-and-down"
            v-model="search"
          ></v-text-field>

          <v-btn color="primary white--text" @click="dialog = true">
            
                      <v-icon left dark>fa fa-user-plus</v-icon>
            Add New Customer</v-btn>
          <v-btn color="success white--text">Excel (xls,csv)</v-btn>
 <v-list two-line subheader v-if="customers" style="background: transparent">
                    <v-flex xs12 style="padding:0 10px;" v-if="customers.length < 1">
                      <vcl-twitch secondary="#FFC107" primary="#fb8c00"></vcl-twitch>
                    </v-flex>
                    <div v-for="(customer, index) in filteredCustomers" :key="index">
                      <!-- <pre>
                      {{ message }}
                      </pre>-->
                      <v-list-tile
                        style="background: #fff; margin-top: 5px; border-radius: 15px"
                        v-if="customer.hasOwnProperty('phone')"
                        avatar
                        v-ripple
                      >
                        <v-list-tile-avatar>
                          <v-icon class="success white--text">fa fa-user-tie</v-icon>
                        </v-list-tile-avatar>

                        <v-list-tile-content>
                          <v-list-tile-title>{{ customer.phone }}</v-list-tile-title>
                          
                <v-list-tile-sub-title v-html="customer.username"></v-list-tile-sub-title>
                        </v-list-tile-content>

                        <v-list-tile-action>
                          <v-btn icon ripple>
                            <v-icon color="blue lighten-1">fa fa-check-double</v-icon>
                          </v-btn>
                        </v-list-tile-action>
                      </v-list-tile>
                    </div>
                  </v-list>
           <!-- <contacts-list></contacts-list> -->

          <!-- <v-list two-line subheader style="background: #f2f2f2">
            <v-subheader inset>Customers</v-subheader>

            <v-list-tile v-for="item in items1" :key="item.title" avatar @click>
              <v-list-tile-avatar>
                <v-icon :class="[item.iconClass]">{{ item.icon }}</v-icon>
              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                <v-list-tile-sub-title>{{ item.subtitle }}</v-list-tile-sub-title>
              </v-list-tile-content>

              <v-list-tile-action>
                <v-btn icon ripple>
                  <v-icon color="grey lighten-1">info</v-icon>
                </v-btn>
              </v-list-tile-action>
            </v-list-tile>
          </v-list> -->


      </v-card>
    </v-flex>
    <v-dialog v-model="dialog" width="500">
            <v-card>
              <v-card-title class="headline orange lighten-2" primary-title>New Category</v-card-title>

              <v-card-text>
                <v-form ref="form" v-model="valid" lazy-validation>
                  <v-layout row wrap style="padding: 10px">
                    <v-flex xs12 sm12 md12>
                      <v-text-field
                        v-model="customerName"
                        :rules="nameRules"
                        label="Username"
                        placeholder="e.g John"
                        required
                        box
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm12 md12>
                      <v-text-field
                        v-model="customerPhone"
                        :rules="nameRules"
                        label="Phone"
                        prefix="+254"
                        placeholder="e.g7XXXXXXXX"
                        required
                        box
                      ></v-text-field>
                    </v-flex>
                  </v-layout>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <!-- <v-btn absolute dark fab top right color="pink">
                      <v-icon>add</v-icon>
                </v-btn>-->
                <v-btn color="red" class="white--text" @click="dialog = false">Cancel</v-btn>
                <v-btn color="primary" flat @click="AddCustomer">Create</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          </v-layout>
      </v-container>
     </v-card>
 </v-app>


</template>

<script>

import firebase from "../service/firebase"; 
import { VclTwitch } from "vue-content-loading";
import ContactTableVue from "./ContactTable.vue";
export default {
     components: {
    "contacts-list": ContactTableVue,
    VclTwitch
  },
    mounted() {
 let menu_code = this.$route.params.menu_id;
    let cmrs = firebase.database
      .ref("global_menus/" + menu_code)
      .child("customers");
    // console.log(obj);
    let vm = this;

  
    cmrs.on("value", function(snapshot, prevChildKey) {
      // vm.menu = newPost
      var newCustomers = snapshot.val();
      vm.customers = newCustomers;
      console.log(newCustomers);
    });

    // await this.menus

    // this.search = this.menus[0].name
  },
data(){
    return {
      customers: null,
      dialog: false,
      search: "",
      customerName: "Guest",
      customerPhone: "713591452",
      valid: true,
      nameRules: [
        v => !!v || "Name is required"
        // v => (v && v.length <= 10) || 'Name must be less than 10 characters'
      ],
        items1: [
      {
        icon: "folder",
        iconClass: "grey lighten-1 white--text",
        title: "Guest #1",
        subtitle: "0713591423"
      },
      
      {
        icon: "folder",
        iconClass: "grey lighten-1 white--text",
        title: "Guest #2",
        subtitle: "0712323232"
      },
      {
        icon: "folder",
        iconClass: "grey lighten-1 white--text",
        title: "Guest #3",
        subtitle: "0712323232"
      }
    ]
    }
},
 computed: {
    
     filteredCustomers() {
       let vm = this
      return _.filter(this.customers, function(customer) {
        return customer.phone.toLowerCase().includes(vm.search.toLowerCase()) || customer.username.toLowerCase().includes(vm.search.toLowerCase())
      });

    }
  },
methods: {
  AddCustomer(){
 let obj = firebase.database
            .ref("global_menus/" + this.$route.params.menu_id)
            .child("customers");

          obj.child(this.customerPhone).update({
            date: -1 * new Date().getTime(),
            phone: "+254"+ this.customerPhone,
            username: this.customerName
          });
          this.dialog = false
  }
}
}
</script>

<style>
.white-bg {
    background: #ffffff;
}
</style>



